@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #ececee;
  font-family: "Raleway";
  color: black;
}

/* Navbar style */
nav {
  background-color: white;
  box-shadow: 0px 5px 5px rgb(20, 20, 20, 0.2);
}
nav a {
  font-size: 130%;
  color: black;
}
nav a:hover {
  color: black;
  transform: scale(1.2);
}
nav a.active {
  border-bottom: #333 1px solid;
}

.nav-icon {
  font-size: 140% !important;
}

/* Blog name */
#blogName {
  position: relative;
  min-height: 500px;
  background: url(/static/media/blog-name-2.737da788.jpg);
  background-position: center;
  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  text-align: center;
  color: #1e1e24;
}

/* Blogs area */
#blogsArea {
  color: #1e1e24;
}

/* Blog post */
.card {
  color: #1e1e24;
  background-color: white;
  box-shadow: 0px 0px 10px rgb(20, 20, 20, 0.2);
  margin-top: 50px !important;
}
.blog-post h3 {
  font-size: 300%;
}
.blog-post p {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: large;
}
.card-footer {
  background-color: #eee;
}
.btn-light {
  background-color: #fff !important;
  box-shadow: 0px 0px 10px rgb(20, 20, 20, 0.1);
  border-radius: 5px;
}
.btn-light:hover {
  background-color: #5a6268 !important;
  color: white !important;
}

.blockquote-footer {
  color: black;
  font-size: 120%;
}
/* Side area */
#sideArea {
  height: 500px;
}

/* Author info */
#authorInfo {
  font-size: 130%;
}

/* Search area */
.search-area {
  background-color: white;
  box-shadow: 0px 0px 10px rgb(20, 20, 20, 0.2);
}

/* Footer */
#mainFooter {
  background-color: white;
  font-size: 150%;
  color: #1e1e24;
  margin-top: 100px !important;
}

/* Blog paragraph */
.blog-paragraph img {
  width: 100%;
  object-fit: cover;
  height: 300px;
}

/* Utils */
.dark-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.7);
}

hr {
  width: 100%;
  border: none;
  height: 3px;
  background-color: #333;
  border-radius: 15px;
}

.display-4 {
  font-size: 250%;
}

.btn-go {
  box-shadow: 0px 0px 10px rgb(20, 20, 20, 0.1);
  background-color: #1e1e24;
  color: white;
}
.btn-go:hover {
  background-color: white;
  color: #1e1e24;
}

.mt-100 {
  margin-top: 100px;
}

@media only screen and (max-width: 600px) {
  .footer-bottom {
    position: relative;
    top: 700px;
  }
}

